
















import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import Tabs from '@/components/Tabs.vue';
import recordTypeList from '@/constants/recordTypeList';
import dayjs from 'dayjs';
import clone from '@/lib/clone';
import Chart from '@/components/Chart.vue';
import _ from 'lodash';

@Component({
  components: {Tabs, Chart},
})
export default class Statistics extends Vue {
  // eslint-disable-next-line no-undef
  tagString(tags: Tag[]) {
    return tags.length === 0 ? '' : tags.map(t => t.name).join('');
  }

  mounted() {
    //
    if (this.$refs.chartWrapper) {
      const div = this.$refs.chartWrapper as HTMLDivElement;
      div.scrollLeft = div.scrollWidth;
    }
  }

  // 
  get lineKeyValueList() {
    // console.log(this.groupedList);
    const today = new Date();
    const array = [];
    for (let i = 0; i <= 29; i++) {
      // this.recordList=[{createdAt:'2022-09-01',amount:1.00,...},{},]
      // this.groupedList=[{title:'2022-09-01',items:[{},{},],total:1.00,...},{},]
      // array=[{key:'2022-09-01',value:1.00},{},]

      const dateString = dayjs(today).subtract(i, 'day').format('YYYY-MM-DD');

      const found = _.find(this.groupedList, {title: dateString});

      array.push({key: dateString, value: found ? found.total : 0});
    }
    array.sort((a, b) => {
      if (a.key > b.key) {
        return 1;
      } else if (a.key === b.key) {
        return 0;
      } else {
        return -1;
      }
    });
    // console.log(array);
    // console.log(this.recordList.map(r => _.pick(r, 'createdAt', 'amount')));
    return array;
  }

  // /
  get pieKeyValueList() {
    // 
    const map = new Map();
    const array: { value: number, name: string }[] = [];
    const today = new Date();

    // 
    // obj keyvalue30
    const obj: Record<string, number> = {};

    // 30
    for (let i = 0; i <= 29; i++) {
      //   found
      const dateString = dayjs(today).subtract(i, 'day').format('YYYY-MM-DD');
      const found = _.find(this.groupedList, {title: dateString});

      // found.items 
      // {1302: 30...}  obj
      let tagName_amount = 0;
      // console.log('found', found);
      if (found?.items) {
        for (let k = 0; k < found.items.length; k++) {
          let tagName = found.items[k].tags[0].name;
          tagName_amount = found.items[k].amount;
          if (tagName in obj) {
            obj[tagName] += tagName_amount;
          } else {
            obj[tagName] = tagName_amount;
          }
        }
      }
    }
    // console.log('obj', obj);
    for (let key in obj) {
      array.push({value: obj[key], name: key});
    }
    // console.log('array', array);
    return array;
  }

  //
  get lineChartOptions() {
    const dates = this.lineKeyValueList.map(item => item.key);
    const values = this.lineKeyValueList.map(item => item.value);
    // console.log(values);
    return {
      //echartspadding(echarts paddingechartsgrid)
      grid: {
        left: 0,
        right: 0,
        top: 10,
        bottom: 30
      },
      xAxis: {
        axisTick: {
          show: true,
          alignWithLabel: true
        },
        axisLabel: {
          // 
          formatter: function (value: string, index: number) {
            // -
            return value.substring(5);
          }
        },
        type: 'category',
        data: dates
      },
      yAxis: {
        type: 'value',
        show: false
      },
      series: [{
        symbolSize: 12,
        data: values,
        type: 'line'
      }],
      tooltip: {show: true}
    };
  }

  // 
  get pieChartOption() {
    const array = this.pieKeyValueList;
    // const values = this.lineKeyValueList.map(item => item.value);
    // console.log('array',array);

    return {
      tooltip: {
        trigger: 'item',
        formatter: '{a} <br/>{b} : {c} ({d}%)',
      },
      legend: {
        type:'scroll',
        top: '5%',
        left: 'left',
        // bottom: '20',
        // padding-bottom 
        padding:[10,10,10,10]
      },
      series: [
        {
          name: '',
          type: 'pie',
          radius: ['40%', '70%'],
          center: ['50%','60%'],
          // 
          avoidLabelOverlap: false,
          bottom: 10,
          // pc   
          // legend
          // legendHoverLink: true,
          // /20px
          selectedOffset: 20,
          itemStyle: {
            borderRadius: 10,
            borderColor: '#fff',
            borderWidth: 2
          },
          label: {
            show: false,
            position: 'center'
          },
          emphasis: {
            label: {
              // legend
              // buildbug
              show: false,
              fontSize: '40',
              fontWeight: 'bold'
            }
          },
          labelLine: {
            show: false
          },
          data: array,
        }
      ]
    };
  }

  beautify(string: string) {
    const day = dayjs(string);
    const now = dayjs();
    if (day.isSame(now, 'day')) {
      return '';
    } else if (day.isSame(now.subtract(1, 'day'), 'day')) {
      return '';
    } else if (day.isSame(now.subtract(2, 'day'), 'day')) {
      return '';
    } else if (day.isSame(now, 'year')) {
      return day.format('MD');
    } else {
      return day.format('YYYYMD');
    }
  }

  get recordList() {
    // eslint-disable-next-line no-undef
    return (this.$store.state as RootState).recordList;
  }

  get groupedList() {
    const {recordList} = this;
    if (recordList.length === 0) {return [];}
    const newList = clone(recordList)
        .filter(r => r.type === this.type)
        .sort((a, b) => dayjs(b.createdAt).valueOf() - dayjs(a.createdAt).valueOf());
    if (newList.length === 0) {return [];}

    type Result = { title: string, total?: number, items: RecordItem[] }[]
    const result: Result = [{title: dayjs(newList[0].createdAt).format('YYYY-MM-DD'), items: [newList[0]]}];

    for (let i = 1; i < newList.length; i++) {
      const current = newList[i];
      const last = result[result.length - 1];
      if (dayjs(last.title).isSame(dayjs(current.createdAt), 'day')) {
        last.items.push(current);
      } else {
        result.push({title: dayjs(current.createdAt).format('YYYY-MM-DD'), items: [current]});
      }
    }
    result.map(group => {
      group.total = group.items.reduce((sum, item) => {
        return sum + item.amount;
      }, 0);
    });
    return result;
  }

  beforeCreate() {
    this.$store.commit('fetchRecords');
  }

  type = '-';
  recordTypeList = recordTypeList;
}
