import Vue from 'vue';
import Component from 'vue-class-component';

const map: { [key: string]: string } = {
  'tag name duplicated': ''
};

@Component
export class TagHelper extends Vue {
  //tag push tags,
  createTag() {
    this.$prompt('', '', {
      confirmButtonText: '',
      cancelButtonText: '',
      inputPattern: /^.{1,4}$/,
      inputPlaceholder: '14',
      inputErrorMessage: '4',
    }).then((name) => {
      this.$message({
        type: 'success',
        message: '',
        duration: 2000,
        // 
        customClass: '.el-message',
      });
      // console.log(name);
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      if ('value' in name) {this.$store.commit('createTag', name.value);}
      if (this.$store.state.createTagError) {
        this.$message({
          type: 'error',
          message: map[this.$store.state.createTagError.message] || '',
          duration: 2000,
        });
      }
    }).catch((err) => { console.log(err)});
  }
}

export default TagHelper;